.Banner {
    background-image: url(/minimalist-portfolio/minimalist-portfolio/static/media/image-homepage-hero@2x.2e4f457f.jpg);
    width: 100%;
    height: 37.5rem;
    background-position: center;
    background-size: cover;
    position: relative;
}

.Banner .Banner_text {
    position: absolute;
    bottom: 0;
    width: 27.8125rem;
    height: 22.3125rem;
    display: flex;
    flex-direction: column;
    background-color: #FAFAFA;
}

.Banner .Banner_text .hero_text {
    flex: 1 1;
    display: flex;
    align-items: center;
    font-family: "Ibarra Real Nova";
    font-size: 3.125rem;
    line-height: 3.125rem;
    letter-spacing: -.45px;
    color: #33323D;
}

.Banner .hero_text h1 {
    font-weight: bold;
}

.Banner .Banner_button {
    background-color: #203A4C;
    color: white;
    width: 12.5rem;
    height: 3rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    position: relative;
}

.Banner .Banner_button a {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 1;
}

.Banner .Banner_button .image_container {
    width: 3rem;
    height: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.Banner .Banner_button .background_filler {
    background-color: black;
    opacity: .1;
    width: 100%;
    height: 100%;
    position: absolute;
}

.Banner .Banner_button img {
    width: 1rem;
    height: .75rem;
    align-self: center;
}

.Banner .Banner_button span {
    flex: 1 0 auto;
    color: white;
    line-height: .875rem;
    font-size: .75rem;
    text-align: center;
    text-transform: uppercase;
    font-family: "Public Sans";
    letter-spacing: 2px;
    font-weight: 100;

}

@media only screen and (max-width: 1130px) {
    .Banner {
        background-image: url(/minimalist-portfolio/minimalist-portfolio/static/media/image-homepage-hero@2x.2e4f457f.jpg);
    }
    .Banner .Banner_text {
        width: 32.125rem;
        height: 17.375rem;
    }

    .Banner .Banner_text .hero_text {
        font-size: 2.5rem;
        line-height: 2.625rem;
        letter-spacing: -.36px;
    }
}

@media only screen and (max-width: 705px) {
    .Banner {
        background-image: url(/minimalist-portfolio/minimalist-portfolio/static/media/image-homepage-hero@2x.2e4f457f.jpg);
        background-size: contain;
        background-repeat: no-repeat;
        background-position: top;
        width: 19.4375rem;
        height: 33.9375rem;
    }

    .Banner .Banner_text {
        width: 19.4375rem;
        height: 16.5rem;
    }

    .Banner .Banner_text .hero_text  {
        font-size: 2.5rem;
        line-height: 2.625rem;
        letter-spacing: -.36px;
    }
}
.AboutMe {
    height: 37.5rem;
    width: 63.4375rem;
    position: relative;
    display: flex;
}

.AboutMe .image_container {
    width: 33.75rem;
