    .AboutMe {
        width: 19.4375rem;
        height: auto;
        flex-direction: column;
    }

    .AboutMe .image_container {
        width: inherit;
        height: 21.625rem;
    }

    .AboutMe .image_container img {
        width: inherit;
        height: inherit;
        object-fit: cover;
    }

    .AboutMe .separator {
        flex: 0 0 2rem;
    }

