.ContactForm {
    width: 100%;
    height: 26.625rem;
    display: flex;
}

.ContactForm h2 {
    font-family: "Ibarra Real Nova";
    font-size: 2.5rem;
    line-height: 2.625rem;
    font-weight: bold;
    letter-spacing: -.36px;
    color: #33323D;
    width: 21.875rem;
}

.ContactForm .separator {
    flex: 1 1;
}

.ContactForm form {
    height: inherit;
    width: 39.6875rem;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}

.ContactForm form div {
    flex: 0 0 5.375rem;
    width: inherit;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}

.ContactForm form div label {
    flex: 0 0 1.875rem;
    display: block;
    color: rgb(51, 50, 61, .8);
    font-family: "Public Sans";
    line-height: 1.875rem;
    font-size: .8125rem;
    font-weight: bold;
}

.ContactForm form div input {
    width: inherit;
    height: 3rem;
    flex: 0 0 3rem;
}

.ContactForm form div:last-child {
    flex: 0 0 8.375rem;
}

.ContactForm form div textarea {
    height: 6rem;
    width: inherit;
    color:#979797;
    font-family: "Public Sans";
    line-height: 1.875rem;
    font-size: .8125rem;
}

.ContactForm form div input, textarea {
    background-color: rgb(51, 50, 61, .1);
    border: none;
    padding-left: 1rem;
}

.ContactForm form div input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
    font-family: "Public Sans";
    line-height: 1.875rem;
    font-size: .8125rem;
    color: rgb(51, 50, 61, .4);
}

.ContactForm form div input:-ms-input-placeholder, textarea:-ms-input-placeholder {
    font-family: "Public Sans";
    line-height: 1.875rem;
    font-size: .8125rem;
    color: rgb(51, 50, 61, .4);
}

.ContactForm form div input::placeholder, textarea::placeholder {
    font-family: "Public Sans";
    line-height: 1.875rem;
    font-size: .8125rem;
    color: rgb(51, 50, 61, .4);
}

.ContactForm form .submit {
    flex: 0 0 3rem;
    width: 12.5rem;
    background-color: #203A4C;
    color: white;
    font-family: "Public Sans";
    font-size: .75rem;
    line-height: .875rem;
    font-weight: 100;
    letter-spacing: 2px;
    text-transform: uppercase;
    padding: 0;
}

@media only screen and (max-width: 1130px) {
    .ContactForm {
        height: 31.25rem;
        flex-direction:  column;
    }

    .ContactForm h2 {
        width: 17.5625rem;
    }

    .ContactForm form {
        height: 26.625rem;
        width: 43.0625rem;
    }
}

@media only screen and (max-width: 705px) {
    .ContactForm {
        height: 30.75rem;
        width: 19.4375rem;
    }

    .ContactForm h2 {
        width: 100%;
    }

    .ContactForm form {
        height: 28.125rem;
        width: inherit;
    }
}
